.Search input {
  background: none;
  border: none;
  padding: 10px 10px 10px 43px;
  background: var(--secondary-bg);
  color: var(--secondary-color);
  border-radius: 4px;
  max-width: 845px;
  width: 100%;
  font-size: 17px;
}
.Search input:focus {
  background: var(--primary-bg);
  outline: 2px solid var(--secondary);
}
.Search {
  display: flex;
  justify-content: center;
  width: 100%;
  max-width: 644px;
  height: 40px;
  margin: 0 auto;
  
}
.Search svg {
  margin: 6px -37px 6px 11px;
  color: #6b6a6a;
  z-index: 1;
  height: 30px;
  width: 30px;
}