.KeyChain .key-id:hover {
  background: var(--secondary-bg);
  padding: 0 8px;
  margin: -1px 8px 0 1px;
  box-shadow: 0px 1px transparent;
  border-radius: 3px;
  color: var(--primary-color);
}
.KeyChain .key-id {
  display: flex;
  transition: .3s;
  line-height: 39px;
  width: calc(100% - 37px);
  color: var(--secondary-color);
  margin-right: 17px;
  transition: .3s background, .3s padding, .3s margin, .3s color;
  margin-top: -1px;
  box-shadow: 0 1px var(--secondary-bg);
  cursor: pointer;
  margin-left: 9px;
}
.KeyChain .section .content{
  overflow-x: unset !important;
}
.actions {
  display: flex;
}


.KeyChain .address {
  margin-left: 10px;
}
.NoKeys{
  text-align: center;
  color: #444;
  width: 300px;
  margin: auto;
}
.NoKeys div {
  height: 200px;
  background: center/contain no-repeat;
  margin-bottom: 21px;
}

.Login div {
  background: center/contain no-repeat;
  height: 310px;
  /*! width: 400px; */
  display: block;
}
