.LoadingView {
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.loading-content{
  min-height: 150px !important;
}
.LoadingView .loading-elements{
  display: none;
  flex-direction: column;
  background: var(--primary-bg-op9);
  background: linear-gradient(var(--primary-bg-op5),var(--primary-bg-op9),var(--primary-bg));
  color: var(--secondary-color);
  text-align: center;
  z-index: 10;
  height: 100%;
  justify-content: center;
  position: relative;
  z-index: 10;
}
.LoadingView.loading  .loading-elements{
  display: flex;
}
.LoadingView .loading-elements svg {
  font-size: 60px;
color: var(--secondary-color);
margin: 0 auto;
  margin-bottom: 0px;
animation: spin linear infinite 2s;
margin-bottom: 17px;
transition:.2s;
}
@keyframes spin {
  0% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
@-moz-keyframes spin {
  0% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
button.disabled{
  background: none !important;
  box-shadow: none !important;
  cursor: not-allowed;
  color: #aeaeae !important;
}
button.disabled span{
  display:none
}