.address{
  color: #468eff;
  font-size: 13px;
  border: 1px solid var(--secondary-bg);
  padding: 2px 7px 2px 2px;
  border-radius: 8px;
  font-weight: bold;
  line-height: 24px !important;
  display: inline-flex;
}
.address img{
  margin-right: 5px;
  height: 25px;
  border-radius: 6px;

}