.Chain{
  width: 32px;
padding: 0px !important;
height: 32px;
margin: 4px;
}
.Chain label {
  display: none;
}
.Chain fieldset {
  box-shadow: none;
  border: none;
}
.Chain .MuiSelect-icon {
  display: none;
}
.Chain .MuiFormControl-root {
  padding: 0;
  margin: 0;
  display: block;
  min-width: auto;
}

.Chain .MuiOutlinedInput-root div {
  padding: 0px !important;
}
.Chain i {
  height: 24px;
  width: 24px;
  display: block;
  background: center/cover;
}