:root {
  --primary: #c44141;
  --secondary: #000;
  --primary-bg: #FAFAFA;
  --secondary-bg: #DDD;
  --primary-color: #333;
  --secondary-color: #3f3f3f;
  --primary-bg-op9: #fafafad2;
  --primary-bg-op5: #fafafa77;

}
