body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--primary-bg);
  color: var(--primary-color);
}
.Header {
  height: 40px;
  padding: 15px 20px;
}
.logo{
  background: 0 -2px/196px no-repeat;
  width: 196px;
  min-width: 196px;
  cursor: pointer;
}
.Header .content, .Footer .content{
  display: flex;
  max-width: 1080px;
  margin: auto;
}
.Header .content{height: 40px}
.header .title{
  font-size: 17px;
  margin: 0px 0 11px;
  box-shadow: 0 1px var(--secondary-bg);
  padding-bottom: 13px;
  width: 100%;
  display: flex;
  line-height: 26px;
  color: var(--secondary);

}
.title svg {
  height: 27px;
  width: 27px;
  margin-right: 11px;
}
.header .title a {
  white-space: nowrap;
  font-weight: bold;
}
.header{
  display: flex
}
.section{
  margin: 5px auto;
  max-width: 600px;
  border: 1px solid var(--secondary-bg);
  padding: 20px;
  border-radius: 4px;
  min-height: 200px;
}

.header button {
  color: var(--primary);
  height: 40px;
  border-radius: 50%;
}


.section .loading-content {
  max-height: calc(100vh - 188px);
  overflow: auto;
}


.Center {
  min-height: calc(100vh - 210px);
}
.Footer {
  background: var(--secondary-bg);
  height: 400px;
}



.MuiPaper-root {
  background: var(--primary-bg) !important;
  color: var(--secondary-color) !important;
}
.MuiDialogTitle-root {
  color: var(--primary);
}
.MuiDialogContentText-root {
  color: var(--secondary-color) !important
}
.header .title a {
  color: var(--secondary);
}
.MuiDialog-container {
  backdrop-filter: blur(10px);
}
.MuiBox-root, .MuiBox-root *{
  background: none !important;
  color: var(--primary-color) !important;
}