
.landing {
  height: 510px;
  background: center/contain no-repeat;
  width: 100%;
}

.Home {
  display: flex;
  height: 500px;
  max-width: 1080px;
  margin: auto;
  padding: 0 25px;
}
.Home p {
  color: var(--secondary-color);
}
.Home .left {
  margin-top: 100px;
}
.Home .title {
  font-size: 40px;
  color: var(--secondary);
  font-weight: bold;
}
.title span {
  color: var(--primary);
  box-shadow: 0 -9px 0 -1px var(--secondary) inset;
  height: 55px;
  display: inline-block;
}


svg .primary {
  fill: var(--primary);
}

svg .secondary {
  fill: var(--secondary);
}