
.KeyDetails .content {
  display: grid;
  grid-template-columns: 108px auto;
  grid-gap: 15px;
  margin: auto;
}
.KeyDetails .active svg {
  color: green;
}
.expired,.revoked {
  color: #d10c0c;
}
.active {
  color: #007400;
}
.active {
  color: #007400;
}
.expired .status, .revoked, .status{
  font-weight: bold;
}
.KeyDetails .col {
  padding: 5px;
  background: var(--secondary-bg);
  color: var(--secondary-color);
  border-radius: 4px;
}
.KeyDetails .content div:not(.col) {
  line-height: 28px;
}

.KeyDetails .owner {
  cursor: pointer;
  color: #b59c76;
}

.PublicKeyContent .MuiDialogContentText-root {
  font-size: 15px;
}
.PublicKeyContent textarea {
  max-height: 200px;
  resize: none;
  max-width: 500px;
  color: var(--primary-color);;
  width: 475px;
  height: 200px;
  background: var(--primary-bg);
  border: 1px solid var(--secondary-bg);
}
.PublicKeyContent textarea:focus,.PublicKeyContent textarea:focus-visible{
  outline: none;
border: 1px solid var(--secondary);
}
.revoke{
  padding: 5px !important;
  border-radius: 50%;
  background: transparent !important;
  color: #721a1a !important;
}

.revoke.failed {
  color:  red !important;
}
.revoke.loading {
  pointer-events: none;
  color: var(--primary-color) !important;
  cursor: wait !important;
}
.btn-confirm {
  background: #5b0404 !important;
  color: #FFF !important;
}
 .btn-confirm:hover {
  background: #4c0303 !important;
}
